html, body, .root {
	height: 100%;
}
body {
	line-height: 1.5;
}
body, .app {
	background: #dee0e2;
}
/* main page */

.list-books-title {
	padding: 10px 0;
	background: #4286f4;
	text-align: center;
	height: 80px;
}
.list-books-title h1 {
	font-weight: 400;
	margin: 0;
	color: white;
}
#hero {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	background-color: #4286f4;
}
.title1 {
	color: white;
	font-size: 45px;
	line-height: .9;
	font-style: oblique;
	text-align: center;
}
.title2 {
	color: white;
	font-size: 45px;
	font-style: oblique;
}
.headersbooks {
	height: 150px;
	width: 150px;
}
.headerbooks {
	height: 150px;
	width: 150px;
}
.list-books-content {
	padding: 0 0 80px;
	-ms-flex: 1 1;
	    flex: 1 1;
}
.bookshelf {
	padding: 0 10px 20px;
}
@media (min-width: 600px) {
	.bookshelf {
		padding: 0 20px 40px;
	}
}
.bookshelf-title {
	border-bottom: 2px solid #4286f4;
}
.bookshelf-books {
	text-align: center;
}
.open-search {
	position: fixed;
	right: 25px;
	bottom: 45px;
}
.open-search a {
	display: block;
	width: 50px;
	height: 50px;
	border-radius: 40%;
	background: #4286f4;
	background-image: url('./icons/add.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 28px;
	-webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	font-size: 0;
}
/* search page */

.search-books-bar {
	position: relative;
	width: 100%;
	top: 0px;
	left: 0;
	z-index: 5;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 0 6px rgba(0, 0, 0, 0.23);
	        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 0 6px rgba(0, 0, 0, 0.23);
}
.search-books-input-wrapper {
	-ms-flex: 1 1;
	    flex: 1 1;
	background: #e9e;
}
.search-books-bar input {
	width: 100%;
	padding: 15px 10px;
	font-size: 1.25em;
	border: none;
	outline: none;
}
.close-search {
	display: block;
	top: 20px;
	left: 15px;
	width: 50px;
	height: 53px;
	background: white;
	background-image: url('./icons/arrow-back.svg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: 28px;
	font-size: 0;
}
.search-books-results {
	padding: 80px 10px 20px;
}
/* books grid */

.books-grid {
	list-style-type: none;
	padding: 0;
	margin: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.books-grid li {
	padding: 10px 15px;
	text-align: left;
}
.book {
	width: 140px;
}
.book-title, .book-authors {
	font-size: 0.4em;
}
.book-title {
	margin-top: 10px;
}
.book-authors {
	color: #737477;
	font-size: 0.3em;
}
.book-top {
	position: relative;
	height: 200px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: end;
	    align-items: flex-end;
}
.book-shelf-changer {
	position: absolute;
	right: 0;
	bottom: -10px;
	width: 40px;
	height: 40px;
	border-radius: 40%;
	background: #4286f4;
	background-image: url('./icons/arrow-drop-down.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 20px;
	-webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}
.book-shelf-changer select {
	width: 100%;
	height: 100%;
	opacity: 0;
	cursor: pointer;
}
/* book cover */

.book-cover {
	-webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	background: #eee;
}
.book-cover-title {
	padding: 20px 10px 0;
	text-align: center;
	font-size: 0.7em;
}
/*Footer*/

.footer {
	position: fixed;
	left: 0;
	-ms-flex-line-pack: center;
	    align-content: center;
	padding: 0;
	bottom: 0;
	text-align: center;
	width: 100%;
	height: 5%;
	color: white;
	background-color: #4286f4;
	font-size: 18px;
}
.footer-text {
	font-size: 7px;
	color: white;
	height: 15px;
	float: center;
}


